<template>
  <div>
  <div class="item"
       v-for="(item, index) of list"
       :key="index"
  >
    <div class="item-title border-bottom">
      <span class="item-title-icon"></span>
      {{item.title}}
    </div>
    <div v-if="item.children" class="item-children">
      <detail-list :list="item.children"></detail-list>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: 'DetailList',
  props: {
    list: Array
  },
  mounted () {
    console.log(this.list)
  }
}
</script>

<style lang="stylus" scoped>
  .item-title-icon
    display: inline-block;
    positon: absolute;
    width: .36rem;
    height: .36rem;
    top: .26rem;
    left: .2rem;
    background: url("") 0 -.45rem no-repeat;
    margin-right: .1rem;
    background-size: .4rem 3rem;
  .item-title
    line-height: .8rem
    font-size: 0.32rem
    padding: 0 .2rem
  .item-children
    padding: 0 .2rem

</style>



