<template>
<div>
  <div class="title">周末去哪儿</div>
  <ul>
    <li class="item"
        v-for="item of weekendList"
        :key='item.id'>
        <div class="item-img-warpper">
           <img class="item-img" :src='item.imgUrl'>
        </div>
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>
    </li>
  </ul>
</div>
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    weekendList: Array
  }
}
</script>

<style lang="stylus" scoped>
  .title
   margin-top: .2rem
   line-height: .8rem
   background: #eee
   text-indent: .2rem
   .item-img-warpper
      overflow: hidden
      height: 0
      padding-bottom: 33.9%
     .item-img
       width:100%
  .item-info
      padding:.2rem
    .item-title
      line-height: .54rem
      font-size: .32rem
      ellipsis()
    .item-desc
       line-height: .4rem
       color: #ccc
       ellipsis()
</style>



