<template>
    <div>
      <city-top></city-top>
      <city-search :cities="cities"></city-search>
      <city-list :cities="cities" :hot="hotCities" :letter="Letter"></city-list>
      <city-alphabet :cities="cities" @change="handleLetterChange"></city-alphabet>
    </div>
</template>

<script>
import axios from 'axios'
import CityTop from './components/Top'
import CitySearch from './components/Search'
import CityList from './components/List'
import CityAlphabet from './components/Alphabet'

export default {
  name: 'City',
  props: {
    letter: String
  },
  components: {
    CityTop,
    CitySearch,
    CityList,
    CityAlphabet
  },
  data () {
    return {
      cities: {},
      hotCities: [],
      Letter: ''
    }
  },
  methods: {
    getCityInfo () {
      axios.get(this.GLOBAL.BASE_URL + '/city.json')
        .then(this.handleGetCityInforSucc)
        .catch(function (error) {
          console.log(error)
        })
    },
    handleGetCityInforSucc (res) {
      console.log(res)
      res = res.data
      if (res.ret && res.data) {
        const data = res.data
        this.cities = data.cities
        this.hotCities = data.hotCities
      }
    },
    handleLetterChange (letter) {
      this.Letter = letter
    }
  },
  mounted () {
    console.log(111)
    this.getCityInfo()
  }
}
</script>

<style scoped>

</style>



