<template>
  <div class="header">
    <div class="header-left"><div class="iconfont icon-back">&#xe624;</div></div>
    <div class="header-input"><span class="iconfont">&#xe632;</span>输入城市/景点/游玩主题</div>
    <router-link to="/City">
      <div class="header-right">{{this.city}}
        <span class="iconcity"></span>
      </div>
    </router-link>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'HomeHeader',
  computed: {
    ...mapState(['city'])
  }
}
</script>

<style  lang="stylus" scoped>
  @import '~styles/varibles.styl'
.header
  display: flex
  line-height: .86rem;
  background-color :$bgColor;
  color : #fff;
 .header-left
    width: .64rem
    float: left
    text-align : center
   .icon-back
     text-align : center
     font-size: 0.4rem
.header-input
   flex:1
   height: .64rem
   line-height : .64rem;
   margin-top: .12rem
   margin-left: .2rem
   background: #fff
   border-radius: .1rem
   color:#ccc
   padding-left: .2rem
 .header-right
    min-width: 1.84rem
    padding: 0 .1rem
    float: right
    text-align : center
    color : #fff
  .iconcity
    margin-left:0.04rem
    width:0px
    height:0px
    border-top: .12rem solid #fff
    border-right: .12rem solid transparent;
    border-bottom: .12rem solid transparent;
    border-left: .12rem solid transparent;
    position:relative
    top:11px

</style>



