<template>
   <div class="icons" >
     <swiper :options='swiperOption' >
       <swiper-slide v-for="(page, index) in pages" :key="index">
     <div class="icon" v-for="item in page" :key="item.id">
      <div class="icon-img"> <img :src="item.imgUrl"></div>
       <p class="icon-desc" v-text="item.desc"></p>
     </div>
       </swiper-slide>
     </swiper>

     </div>
</template>

<script>
export default {
  name: 'HomeIcons',
  props: {
    iconList: Array
  },
  data () {
    return {
      swiperOption: {
        autoplay: false
      }
    }
  },
  mounted () {
    console.log(this.iconList)
  },
  computed: {
    pages () {
      const pages = []
      this.iconList.forEach((item, index) => {
        const page = Math.floor(index / 8)
        if (!pages[page]) {
          pages[page] = []
        }
        pages[page].push(item)
      })
      return pages
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~styles/varibles.styl'
  @import '~styles/mixins.styl'
 .icons >>> .swiper-container
    height: 0
    padding-bottom: 50%
  .icons
    margin-top: .2rem
  .icon
    position : relative
    overflow hidden
    width:25%
    height: 0
    float:left
    padding-bottom: 25%

.icon-img
    position:absolute
    top:0
    left:0
    right:0
    bottom: .44rem
    box-sizing: border-box
    padding: .1rem

  .icon-img img
    height:100%;
    display: block
    margin:0 auto
  .icon-desc
    padding:0 4px
    position: absolute
    left:0
    right:0
    bottom: 0
    height: .44rem
    line-height: .44rem
    color: $darkTextColor
    text-align: center
    ellispsis()
</style>



